/** @format */

import React, { useState } from "react"
import "./App.css"
import LanderPage from "./views/LanderPage/LanderPage"
import Navi from "./views/Navi/Navi"
import CHeader from "./views/Header/Header"
import FavoritesPage from "./views/FavoritesPage/FavoritesPage"
import SearchPage from "./views/SearchPage/SearchPage"
import MyPage from "./views/MyPage/MyPage"
import { HashRouter as Router, Switch, Route } from "react-router-dom"
import "antd/dist/antd.css"
import { Layout } from "antd"
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons"
import DetailPage from "./views/DetailPage/DetailPage"
import ScrollToTop from "react-scroll-up"
const { Header, Sider, Content } = Layout

function App() {
    return (
        <div className="App">
            <Router>
                <Layout>
                    <Layout className="site-layout">
                        <Header className="site-layout-background">
                            <CHeader></CHeader>
                        </Header>
                        <Content className="site-layout-background">
                            <Switch>
                                <Route exact path="/" component={LanderPage} />
                                <Route
                                    path="/detail/:movId"
                                    component={DetailPage}
                                />
                                <Route
                                    path="/favorites"
                                    component={FavoritesPage}
                                />
                                <Route path="/mypage" component={MyPage} />
                                <Route
                                    path="/search/:searchWord"
                                    component={SearchPage}
                                />
                            </Switch>
                            <ScrollToTop showUnder={160}>
                                <span className="btn-top">UP</span>
                            </ScrollToTop>
                        </Content>
                    </Layout>
                </Layout>
            </Router>
        </div>
    )
}

export default App
